import React from "react";
import * as Utils from "../../lib/utils";

export default class FormInputRow extends React.PureComponent {
  state = {
    value: ""
  };

  componentDidMount() {
    this.setState({
      value: this.setValue(this.props.defaultValue)
    });
  }

  componentDidUpdate(prevProps) {
    if (prevProps.defaultValue !== this.props.defaultValue) {
      this.setState({
        value: this.setValue(this.props.defaultValue)
      });
    }
  }

  setValue = value => {
    let formatted;
    switch (this.props.format) {
      case "number":
        formatted = Utils.number(value);
        break;

      case "currency":
        formatted = this.props.readOnly
          ? Utils.currency(value)
          : Utils.commas(value);
        break;

      case "float":
        formatted = Utils.float(value);
        break;

      default:
        formatted = value;
        break;
    }
    return formatted;
  };

  updateOnBlur = () => {
    this.setState(
      prevState => ({
        value: this.setValue(prevState.value)
      }),
      () => {
        if (this.props.onBlur) {
          this.props.onBlur(this.props.name, this.state.value);
        }
      }
    );
  };

  render() {
    const { label, name, readOnly, format } = this.props;

    return (
      <div className="form-group">
        <label>{label}</label>
        <div className="input-group">
          {format && format === "currency" && !readOnly && (
            <div className="input-group-prepend">
              <span className="input-group-text">$</span>
            </div>
          )}
          {!readOnly ? (
            <input
              type="text"
              className="form-control"
              name={name}
              value={this.state.value}
              onChange={e => this.setState({ value: e.target.value })}
              onBlur={this.updateOnBlur}
              readOnly={readOnly}
            />
          ) : (
            <div className="value">{this.state.value}</div>
          )}
        </div>
      </div>
    );
  }
}
