import React from "react";
import { Consumer } from "../roi.context";

class SectionCalculator extends React.Component {
  render() {
    return (
      <Consumer>
        {({ calcCost, calcProductivity, calcNet, calcROI }) => (
          <div className="section calculator footer-col">
            <div className="wrapper">
              <div className="title">Cost: </div>
              <div className="value">{calcCost}</div>
            </div>
            <div className="wrapper">
              <div className="title">Productivity: </div>
              <div className="value">{calcProductivity}</div>
            </div>
            <div className="wrapper">
              <div className="title">Net Value: </div>
              <div className="value">{calcNet}</div>
            </div>
            <div className="wrapper">
              <div className="title">ROI: </div>
              <div className="value">{calcROI}</div>
            </div>
          </div>
        )}
      </Consumer>
    );
  }
}

export default SectionCalculator;
