import React from "react";
import {
  SectionCost,
  SectionTeam,
  SectionProductivity,
  SectionOptimization,
  SectionCalculator
} from "./sections";
import * as Lib from "./roi-calculator.lib";
import { Provider } from "./roi.context";

export default class RoiCalculator extends React.PureComponent {
  timer = null;
  previousKey = null;
  previousValue = null;
  state = {
    hasInitialized: false
  };
  componentDidMount() {
    Lib.initDefaultValues.call(this);
  }

  onUpdate = async (key, value, formattedValue) => {
    if (key === this.previousKey && value === this.previousValue) {
      return;
    }
    this.previousKey = key;
    this.previousValue = value;
    await Lib.updateStateValues.call(this, key, value, formattedValue);
    Lib.updateCalculations.call(this, key, value, formattedValue);
  };
  render() {
    const contextValue = this.state;
    return (
      <React.Fragment>
        {this.state.hasInitialized && (
          <Provider value={contextValue}>
            <div>
              <div className="container">
                <div className="row">
                  <div className="col-lg-12">
                    <h1 className="header-title">Outreach ROI Calculator</h1>
                  </div>
                </div>
                <div className="row calculator-row">
                  <div className="col-lg-9 bg-white calculator-wrapper p-0">
                    <SectionTeam onUpdate={this.onUpdate} />
                    <SectionCost onUpdate={this.onUpdate} />
                    <SectionOptimization onUpdate={this.onUpdate} />
                    <SectionProductivity onUpdate={this.onUpdate} />
                  </div>
                  <div className="col-lg-3 d-none d-lg-block">
                    <div className="adwords-banner bg-white w-100">
                      <p>Google AdWords Banner</p>
                    </div>
                  </div>
                </div>
              </div>
              <div className="footer-container">
                <div className="container">
                  <div className="row h-100">
                    <div className="col-md-9 footer-col">
                      <SectionCalculator />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </Provider>
        )}
      </React.Fragment>
    );
  }
}
