import React, { Component } from "react";
import Slider from 'rc-slider';

import 'rc-slider/assets/index.css';
import 'rc-tooltip/assets/bootstrap.css';

export default class withSlider extends Component {
  constructor(props) {
    super(props);

    this.state = {
      description: props.descriptions[0],
      value: 0,
    }

    this.props.onChange(props.keyName, 0, 0);
  }

  onSliderChange = (sliderValue) => {
    this.setState({value: sliderValue, description: this.props.descriptions[sliderValue/25]});
    this.props.onChange(this.props.keyName, sliderValue, sliderValue);
  }

  render() {
    return (
      <div className="slider-wrapper">
        <div className="label">{this.props.label}</div>
        <div className="col-md-10 p-0 description">{this.state.description}</div>
        <div className="col-md-9 p-0 slider">
          <Slider
            value={this.state.value}
            onChange={this.onSliderChange}
            marks={{ 0: "0%", 25: "25%", 50: "50%", 75: "75%", 100: "100%" }}
            step={25}
            handleStyle={{ border: "4px solid #FF7348", width: '22px', height: '22px', boxShadow: '0px 3px 11px rgba(217, 8, 69, 0.34)' }}
            railStyle={{height: '10px', borderRadius: 'unset', backgroundColor: 'white' }}
            trackStyle={{height: '10px', borderRadius: 'unset', backgroundColor: '#FF7348' }}
            dotStyle={{ display: 'none' }}
          />
        </div>
      </div>
    )
  }
}